.container {
    display: flex;
    flex-direction: column;
    font-family: 'Titillium Web', sans-serif;
    font-weight: bold;
    justify-content: space-around;
}

.top {
    display: flex;
}

.left, .right {
    text-align: left;
    display: flex;
    flex-direction: column;
    border: solid #794a00;
    height: auto;
    margin: 10px;
    flex-basis: 20%;
    justify-content: space-around;
}

.left {
    flex-basis: 70%;
    font-size: 60px;
    margin: 10px;
}

.right {
    flex-basis: 30%;
}

.right-sub {
    width: auto;
    margin: 2px;
}

.middle {
    display: flex;
    flex-wrap: wrap;
}

.middle-left {
    justify-content: center;

}

.middle-right, .middle-left {
    width: 50%;
    display: flex;
    flex-flow: wrap;
    text-align: center;
}




.card-container ,.card-container-pie{
    margin: 10px;
    font-family: 'Titillium Web', sans-serif;
    display: flex;
    border: solid #00F9FF;
    -webkit-animation: shadowBox 3s infinite;
            animation: shadowBox 3s infinite;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    flex-direction: column;
    justify-content: center;
    width: 235px;
    border-radius: 15px;
}

.card-container-pie {
    width: 335px;
}

.card-top {
    background-color: #00f9ff;
    color: black;
    border-bottom: solid #484848;
    border-radius: 9px;

}
@-webkit-keyframes shadowBox {
    0% {box-shadow: 2px 1px 18px #00f9ff;}
    50% {box-shadow: 2px 1px 5px #00999d;}
    100% {box-shadow: 2px 1px 18px #00f9ff;}

}
@keyframes shadowBox {
    0% {box-shadow: 2px 1px 18px #00f9ff;}
    50% {box-shadow: 2px 1px 5px #00999d;}
    100% {box-shadow: 2px 1px 18px #00f9ff;}

}

.card-context {
    font-size: 60px;
    height: auto;
    line-height: 111px;
}

.card-context-pie {
    font-size: 80px;
    width: 335px;

}

.card {
    border-radius: 15%;
}
